<template>
    <div class="subscribe">
      <h2 class="primary-color">Subscribe in my newsletter</h2>
      <p class="secondary-color">Keep up with all the news from the world of with my weekly news and get exclusive access to <br />my best offers.</p>
      <form @submit.prevent="addSubscriber">
        <div class="form-group">
          <input 
            type="email"
            placeholder="Your best email"
            class="form-control"
            v-model="email"
          >
          <button class="btn-subscribe">
            <i class="fa-solid fa-check"></i>
          </button>
        </div>
      </form>
      <div class="alert-success">
        <span>Email has been registered successfully! Coming soon you'll receive all my best hotoffers.</span>
      </div>
    </div>
</template>

<script>
import usersColRef from '@/firebase'
import { addDoc } from '@firebase/firestore'


export default {
    data() {
        return {
            email: null,
        }
    },
    methods: {
        async addSubscriber () {
            await addDoc(usersColRef, this.$data)
            document.querySelector('.alert-success').classList.add("actived-alert")
            this.email = null
            this.$router.push('/')
        }
    }
}
</script>

<style scoped>
.subscribe {
  width: 90%;
  max-width: 1728px;
  margin: 5rem auto;
  display: table;
  text-align: center;
}
.subscribe h2 {
  font-size: 25px;
  font-weight: 600;
}
.form-group {
  padding: 10px 25px;
  border-radius: 10px;
  border: 1px solid #EAEAEA;
  background: #FAFAFA;
  display: flex;
  justify-content: space-between;
  max-width: 400px;
  margin: 1rem auto;
}
.form-control {
  font-size: 16px;
  color: var(--secondary-color);
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  border: none;
  background: none;
  outline: none;
  transition: .3s ease all;
}
.btn-subscribe {
  border: none;
  background: none;
  font-size: 16px;
  color: var(--primary-color);
  cursor: pointer;
  transition: .3s ease all;
}
::placeholder {
  font-style: italic;
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
  color: #A6A6A6;
  font-weight: 400;
}

.alert-success {
    padding: 10px 25px;
    border-radius: 10px;
    border: 1px solid #C3E6CB;
    color: #155724;
    box-shadow: 5px 0 15px rgba(212, 237, 218, .5);
    background: #D4EDDA;
    display: none;
    margin: 0 auto;
}
.actived-alert {
    display: table !important;
    animation: 0.5s subsFade cubic-bezier(0.39, 0.575, 0.565, 1);
}
@keyframes subsFade {
    from { opacity: 0; transform: translateY(-1rem); }
    to { opacity: 1; transform: translateY(0); }
}
</style>