<template>
    <section class="main-header">
        <div class="container flex justify-between align-center">
            <router-link to="/" class="nav-brand" @click="toggleNav">
                <img :src="logo" alt="">
            </router-link>

            <input type="checkbox" id="check">
            <label for="check" class="toggle-nav">
                <span></span>
                <span></span>
                <span></span>
            </label>
            
            <nav class="main-nav">
                <router-link to="/about" class="nav-link" @click="toggleNav">About me</router-link>
                <a href="https://github.com/acioliwilson/" target="_blank" class="nav-link" @click="toggleNav">Latest Jobs</a>
                <a href="mailto:w.aciolib@gmail.com" class="btn btn-outline btn-outline-primary" @click="toggleNav">Say hi!</a>
            </nav>
        </div>
    </section>
</template>

<script>
export default {
    name: 'MainHeader',
    data() {
        return {
            logo: 'logo.png',
            isToggled: false,
            isActive: false
        }
    },
    methods: {
        async toggleNav () {
            document.getElementById("check").checked = false
        }
    }
}
</script>

<style scope>
.main-header {
    width: 100%;
    display: flex;
    justify-content: center;
}
.container {
    width: 90%;
    max-width: 1728px;
    padding: 1rem 0;
    position: relative;
}
.flex { display: flex; }
.justify-between { justify-content: space-between; }
.align-center { align-items: center; }
.main-nav {
    display: flex;
    align-items: center;
}
.nav-link {
    color: #000;
    font-weight: 500;
    margin: 0 2rem 0 0;
    font-size: 16px;
}
.btn {
    padding: 10px 25px;
    border-radius: 10px;
    font-size: 16px;
    font-weight: 500;
    transition: .3s ease all;
}
.btn-outline { border: 1px solid transparent; }
.btn-outline-primary { border-color: var(--primary-color); color: var(--primary-color); }
.btn-outline-primary:hover { background: var(--primary-color); color: #FFF; box-shadow: 0 5px 10px rgba(255, 51, 0, .3); }
.mobile-nav, .toggle-btn {
    display: none;
}

@media (max-width: 1024px) {
    #check { display: none; }
    .toggle-nav {
        width: 30px;
        height: 30px;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        cursor: pointer;
        transition: .3s ease all;
    }
    .toggle-nav span {
        width: 25px;
        height: 2px;
        background: var(--primary-color);
        transition: .3s ease all;
    }

    nav {
        width: 100%;
        height: 100vh;
        position: fixed;
        top: 70px;
        left: -100%;
        background: #FFF !important;
        display: flex;
        flex-direction: column;
        align-items: flex-start !important;
        gap: 20px;
        padding: 3rem;
        z-index: 9999;
        transition: .4s ease-out all;
    }
    nav a {
        opacity: 0;
        transform: translateX(-1rem);
    }
    nav a:nth-child(3) {
        border: none;
        padding: 0;
        color: #262626;
    }
    nav a:nth-child(3):hover {
        background: none;
        color: #262626;
        box-shadow: none !important;
    }
    #check:checked ~ .toggle-nav {
        transform: translateY(-5px);
    }
    #check:checked ~ .toggle-nav span:nth-child(1) {
        transform: translateX(-1rem);
        opacity: 0;
    }
    #check:checked ~ .toggle-nav span:nth-child(2) {
        transform: rotate(45deg) translateY(5px);
    }
    #check:checked ~ .toggle-nav span:nth-child(3) {
        transform: rotate(-45deg) translateY(-5px);
    }
    #check:checked ~ nav {
        left: 0;
    }
    #check:checked ~ nav a {
        animation: .8s swipe ease forwards;
        font-size: 20px;
    }
    #check:checked ~nav a:nth-child(1) {
        animation-delay: .2s;
    }
    #check:checked ~nav a:nth-child(2) {
        animation-delay: .4s;
    }
    #check:checked ~nav a:nth-child(3) {
        animation-delay: .6s;
    }
    @keyframes swipe {
        from { opacity: 0; transform: translate(-.5rem); }
        to { opacity: 1; transform: translate(0rem); }
    }
}

#check { display: none; }

@media (min-width: 1025px) {
    .toggle-nav { display: none; }
}
</style>